<template>
  <div class="user">
    <div class="user-search">
      <div class="user-search-title">超级会员激活</div>
      <div class="user-search-input">
        <van-field
          class="input"
          v-model="mobile"
          type="tel"
          placeholder="输入手机号"
        />
        <van-button class="button" round type="info" @click="onButton"
          >搜索</van-button
        >
      </div>
    </div>
    <!-- item -->
    <div class="user-item" v-if="userItem">
      <div class="user-item-content">
        <div class="title">
          <div class="title-name">{{ userItem.nickName }}</div>
          <div class="title-phone">
            <span style="vertical-align: super; margin-right: 12px">{{
              userItem.mobile
            }}</span
            ><van-icon name="envelop-o" size="26" />
          </div>
        </div>
        <div class="context">
          <div class="context-left">
            <van-cell
              class="cell"
              title="ID"
              title-class="cell-left"
              value-class="cell-right"
              :value="userItem.userId"
            />
            <van-cell
              class="cell"
              title="会员类型"
              title-class="cell-left"
              value-class="cell-right"
              :value="userItem.superVipName"
            />
            <van-cell
              class="cell"
              title="最新购买时间"
              title-class="cell-left"
              value-class="cell-right"
              :value="userItem.lastBuyTime"
            />
          </div>
          <div
            class="context-right"
            v-if="userItem.superVipStatus === 0"
            style="background: #ff770f"
          >
            <span>待激活</span>
          </div>
          <div
            class="context-right"
            v-if="userItem.superVipStatus === 1"
            style="background: #2176cf"
          >
            <span>已激活</span>
          </div>
        </div>
        <div class="button">
          <van-cell
            class="cell"
            title="生日"
            title-class="cell-left"
            value-class="cell-right"
            :value="userItem.birthday"
          />
          <van-cell
            class="cell"
            title="地址"
            title-class="cell-left"
            value-class="cell-right"
            :value="userItem.receiptAddress"
          />
        </div>
      </div>
    </div>
    <!-- 空白页 -->
    <div class="bg" v-else>
      <img src="@/assets/white-bg.png" />
    </div>
  </div>
</template>
<script>
import { vipListApi } from "@/api/superVip";

export default {
  data() {
    return {
      mobile: "",
      page: 1,
      size: 10,
      userItem: "",
    };
  },
  methods: {
    onButton() {
      if (!this.mobile) return;
      vipListApi(this.mobile, this.page, this.size).then((res) => {
        this.userItem = res.data.data.items[0];
        console.log(this.userItem);
      });
    },
  },
};
</script>
<style lang='scss' scoped>
.cell {
  font-size: 12px;
  text-align: left;
  padding: 5px 0px;
  color: #344357;
  .cell-left {
    width: 100px;
    flex: initial;
    color:#91A0B2;
  }
  .cell-right {
    text-align: left;
    color:#344357;
  }
}
.user {
  height: 100vh;
  background: #f3f3f3;
  &-search {
    padding: 0px 28px;
    height: 137px;
    background: #344357;
    overflow: hidden;
    &-title {
      text-align: left;
      padding-top: 35px;
      padding-bottom: 20px;
      font-size: 20px;
      color: white;
    }
    &-input {
      display: flex;
      justify-content: space-between;
      margin: auto;
      .input {
        width: 240px;
        height: 36px;
        padding-top: 8px;
        padding-bottom: 8px;
        border-radius: 4px;
        border: #91a0b2;
      }
      .button {
        width: 64px;
        height: 36px;
      }
    }
  }
  &-item {
    &-content {
      margin-top: 20px;
      width: 375px;
      height: 228px;
      background: white;
      .title {
        display: flex;
        justify-content: space-between;
        align-items: center;
        width: 320px;
        height: 43px;
        margin: 0 auto;
        border-bottom: 1px solid #707070;
        font-weight: Medium;
        &-name {
          font-size: 18px;
          color: #344357;
          font-weight: 500;
        }
        &-phone {
          font-size: 14px;
          color: #91a0b2;
        }
      }
      .context {
        padding-top: 6px;
        position: relative;
        width: 320px;
        height: 105px;
        margin: 0 auto;
        border-bottom: 1px solid #707070;
        &-left {
        }
        &-right {
          position: absolute;
          top: 16px;
          right: 0px;
          font-size: 14px;
          color: white;
          line-height: 36px;
          width: 90px;
          height: 36px;
          border-radius: 18px;
        }
      }
      .button {
        padding-top: 6px;
        width: 320px;
        margin: 0 auto;
      }
    }
  }
  .bg {
    height: calc(100% - 137px);
    position: relative;
    img {
      width: 185px;
      height: 176px;
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
    }
  }
}
</style>
