<template>
  <div class="login">
    login
    <van-form @submit="onSubmit">
      <van-field
        v-model="username"
        name="username"
        label="用户名"
        placeholder="用户名"
        :rules="[{ required: true, message: '请填写用户名' }]"
      />
      <van-field
        v-model="password"
        type="password"
        name="password"
        label="密码"
        placeholder="密码"
        :rules="[{ required: true, message: '请填写密码' }]"
      />
      <div style="margin: 16px">
        <van-button round block type="info" native-type="submit">
          提交
        </van-button>
      </div>
    </van-form>
  </div>
</template>
<script>
import { loginApi } from "@/api/login";
import { setToken } from "@/utils/auth";

export default {
  data() {
    return {
      username: "",
      password: "",
    };
  },
  methods: {
    onSubmit(values) {
      loginApi(values.username, values.password).then((res) => {
        if (res.data.errno === 0) {
          setToken(res.data.data)
          this.$router.push('/user')
        }
      });
    },
  },
};
</script>
<style lang='scss' scoped>
</style>